import React from "react";
import { ProductCompare } from "./general/style/style";
//import { toJS } from "mobx";

export default function ProductSelectDropdown({ brands, models, modelSide, onBrandSelected, onModelSelected }) {

  return (
    <ProductCompare className="borderRight">
      <ul>
        <li>
          <select onChange={(e) => onBrandSelected(e.target.value, modelSide)}>
            <option value="#">Add Brand</option>
            {brands &&
              brands.map((el) => (
                <option key={el.id} value={el.id}>
                  {el.name}
                </option>
              ))}
          </select>
        </li>
        <li>
          <select onChange={(e) => onModelSelected(e.target.value, modelSide)}>
            <option value="#">Add Device</option>
            {models &&
              models.map((el) => (
                <option key={el.id} value={el.id}>
                  {el.name}
                </option>
              ))}
          </select>
        </li>
      </ul>
    </ProductCompare>
  );
}

